@import './colors.scss';

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background-color: $BACKGROUND;
}

// Content

.row {
    margin: 0 10% 0 10%;
    display: flex;
    color: $SECONDARY_COLOR;
    text-align: justify;
}

.column {
    padding: 1%;
    flex: 50%;
    justify-content: center;
    align-items: center;
}

.content-headline {
    font-size: 80px;
    font-weight: 400;
}

.content-wrapper {
    position: relative;
    padding-bottom: 56.25%;
}

.tedx-iframe {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
}
